/**
 * Represents the supported error types
 */
enum ErrorType {
    UnAuthorized,
    Argument,
    FileNotFound,
    DirectoryNotFound,
    HttpClientError,
    HttpServerError,
    JsonParseError,
    RequestTimedOut,
    ArgumentNull,
    SearchServiceNotAvailable,
    Unknown = 10000,
}

export default ErrorType;
